<template>
  <div>
    <v-card id="workExperiences" class="common-card mb-6">
      <v-card-title class="my-4">{{ terms.experiences }}</v-card-title>
      <v-card-text>
        <span class="work-exp">
          <ul class="pa-0">
            <li style="list-style: none" v-for="(item, index) in experiences" :key="index">
              <!-- Company name -->
              <h3 class="greyColor--text">{{ item[lang + 'name'] }}</h3>
              <!-- Duration of experience -->
              <p class="mb-0 mt-1 grey--text text--darken-1" :class="!isLtr ? 'persian-font' : ''">
                {{ item[lang + 'duration'] }}
              </p>
              <!-- Location of experience -->
              <p class="grey--text mb-2 text--darken-1">
                {{ item[lang + 'location'] }}
              </p>
              <!-- Position of experience -->
              <p
                class="mt-0 greyColor--text fs-small"
                :style="
                  isLtr
                    ? 'padding-left: 10px; border-left: 3px solid ' +
                      (!$vuetify.theme.dark ? '#406882' : '#9D5353')
                    : 'padding-right: 10px; border-right: 3px solid ' +
                      (!$vuetify.theme.dark ? '#406882' : '#9D5353')
                "
                :class="!$vuetify.theme.dark ? 'text--darken-1' : ''"
              >
                {{ item[lang + 'position'] }}
              </p>
            </li>
          </ul>
        </span>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import utilities from './../mixin/utilities';
export default {
  mixins: [utilities],
};
</script>
